al .capitalBox > div {\n  margin: 0.3rem 0;\n}\n.beginHome .capitalWindow .capital .capitalBox > div p {\n  font-size: 0.21rem;\n  color: #d4a564;\n}\n.beginHome .capitalWindow .capital .capitalBox > div p:last-child {\n  margin: 0.1rem 0;\n  font-size: 0.21rem;\n  color: #f3d8a1;\n}\n.beginHome .capitalWindow .capital .close {\n  position: absolute;\n  top: 0;\n  right: 0.4rem;\n  height: 1rem;\n  line-height: 1rem;\n}\n.beginHome .capitalWindow .capital .close svg {\n  fill: #fff;\n}\n.beginHome .beginNav {\n  padding: 0.54rem 0.045rem 0;\n}\n.beginHome .beginNav .beginFeatures {\n  display: inline-block;\n  width: 0.7rem;\n  height: 0.7rem;\n  background-color: rgba(26, 18, 13, 0.8);\n  border-radius: 0.1rem;\n  border: solid 0.02rem rgba(183, 177, 142, 0.5);\n  float: right;\n  margin-right: 0.16rem;\n  cursor: pointer;\n}\n.beginHome .beginNav .beginFeatures:last-child {\n  float: left;\n  margin-right: 0;\n  margin-left: 0.36rem;\n  width: 1.07rem;\n}\n.beginHome .selectMoney {\n  width: 4.02rem;\n  height: 0.71rem;\n  line-height: 0.68rem;\n  margin: 0rem auto 0;\n  background: url('../../static/img/seletc.png') no-repeat 50% 50%;\n  background-size: 100% 100%;\n  font-size: 0.32rem;\n  color: #fef2ba;\n  font-weight: bold;\n  clear: both;\n  position: absolute;\n  bottom: 0.8rem;\n  left: calc(50% - 2.01rem);\n}\n.beginHome .gameEntrance {\n  position: absolute;\n  width: 100%;\n  height: calc(100vh - 40vh);\n  top: 40vh;\n  left: 0;\n  text-align: center;\n}\n.beginHome .gameEntrance .lotteryDiv {\n  cursor: pointer;\n}\n.beginHome .gameEntrance .animateBox {\n  background: url('../../static/img/round.png') no-repeat 50% 50%;\n  background-size: 100% 100%;\n  animation: rot 15s linear infinite;\n  position: absolute;\n  top: 0;\n  left: 0;\n  border-radius: 50%;\n  width: 100%;\n  height: 100%;\n  transform-origin: 1.19rem 1.196rem;\n}\n.beginHome .gameEntrance .entranceBox {\n  width: 1.62rem;\n  height: 1.62rem;\n  border-radius: 50%;\n  background: url('