import {mapState} from 'vuex'
export default {
    data(){
        return{
            parWindow:false,
            walletArr:[],
        }
    },
    computed:{
        ...mapState(['showWaller'])
    },
    watch:{
        showWaller(val,val2){
            this.parWindow = val;
            this.walletArr = [];
            let walletObj = JSON.parse(window.sessionStorage.getItem('playerAddr'));
            for(let v in walletObj){
                this.walletArr.push(JSON.parse(walletObj[v]))
            }
            this.walletArr.forEach((v,i)=>{
                if(sessionStorage.getItem('walletIndex')){
                    i==sessionStorage.getItem('walletIndex')?v.click=true:v.click=false;
                }else{
                    i==0?v.click=true:v.click=false;
                }
            })
        },  
    },
    created(){
        
    },
    mounted(){
        
    },
    methods:{
        submitBtn(){
            this.$store.commit('SHOWWALLET',false);
        },
        changeWallet(parmas){
            this.walletArr.forEach((v,i)=>v.click=false);
            parmas.item.click = true;
            this.walletArr = Object.assign([{}], this.walletArr)
            this.$store.commit('WALLETADRESS',parmas.item)
            sessionStorage.setItem('walletIndex', parmas.index);
        }
    }
}



