
import Toast from 'vux/src/components/toast/index.vue'
import Loading from 'vux/src/components/loading/index.vue'

import {mapState} from 'vuex'
import Wallet from 'c@/wallet.vue'
import { setInterval, clearInterval } from 'timers';
export default {
data(){
return{
walletArr:[],
lanage:'Cn',
capitalPop:false,
showPositionValue:false,
toastText:'',

moneyType:[
{className:'usdxBox',bgClassName:'bg5',bgTextClassName:'text5',val:'usdx'},
{className:'bcbBox',bgClassName:'bg6',bgTextClassName:'text6',val:'bcbt'},
{className:'dcBox',bgClassName:'bg7',bgTextClassName:'text7',val:'dc'},
{className:'xtBox',bgClassName:'bg8',bgTextClassName:'text8',val:'xt'},
],
loading:false,

conAddrData:[],
loadNum:0,
count:0,
audioKey:false,
isLoading:false,
musicArr:[
{zName:'音乐',eName:'music',open:true},
{zName:'音效',eName:'soundEffects',open:true}
]
}
},
computed:{
...mapState([
'langChange',
'isLoad',
'bgSound',
'otherSound',
])
},
components:{
Wallet,
Toast,
Loading,
},
created(){
window.enterPortscreen();
window.enterFullscreen();
window.final();
},
beforeMount(){
},
mounted(){
if(!this.isLoad){
this.preload();
}
this.langChange?this.lanage = 'Zh' : this.lanage = 'Cn';
let walletObj = null;
let timer = setInterval(()=>{
if(walletObj==null){
walletObj = JSON.parse(window.sessionStorage.getItem('playerAddr'));
}else{
clearInterval(timer);
let arr = [];
for(let v in walletObj){
arr.push(JSON.parse(walletObj[v]));
}
let thisAddr = window.localStorage.getItem('thisAddr');
if(thisAddr=='undefined'||thisAddr=='null'||!thisAddr){
this.$store.commit('WALLETADRESS',arr[0]);
this.$store.commit('SHOWWALLET',true);
sessionStorage.setItem('walletIndex',0);
}else{
let hasWallet = false;
let thisAddrObj = JSON.parse(thisAddr);
for(let v in arr){
if(thisAddrObj.address==arr[v].address){
this.$store.commit('WALLETADRESS',arr[v]);
this.$store.commit('SHOWWALLET',false);
sessionStorage.setItem('walletIndex',v);
hasWallet = true;
continue;
}
}
if(!hasWallet){
this.$store.commit('WALLETADRESS',arr[0]);
this.$store.commit('SHOWWALLET',true);
sessionStorage.setItem('walletIndex',0);
}
}
}
},300)
this.contractAdress();
document.addEventListener('touchstart',this.comeGame)
document.addEventListener('click',this.comeGame)
},
destroyed(){
this.loading = false;

},
watch:{
langChange(val,val2){
val?this.lanage = 'Zh' : this.lanage = 'Cn';
}
},
methods:{
initVoic(){
var bgSound = this.$tools.getLocal('bgSound');
if(bgSound){
var musicEle0 = document.getElementById('audio1');
this.$store.commit('BGSOUND',bgSound=="true"?true:false);
bgSound=="true"?this.musicArr[0].open=true:this.musicArr[0].open=false;
if(this.bgSound&&this.isLoad){
console.log(musicEle0.paused)
console.log(window.device())
if(window.device()=='Android'&&musicEle0.paused){
musicEle0.load();
musicEle0.play();
}
if(window.device()!='Android'&&!musicEle0.paused){
console.log(1)
document.addEventListener('touchstart',this.touchStart);
}
if(musicEle0.paused){
musicEle0.load();
musicEle0.play();
}
}else if(!this.bgSound){
musicEle0.pause();
}
}else{
this.$tools.setLocal('bgSound',true);
this.musicArr[0].open=true;
document.addEventListener('touchstart',this.touchStart);
}
},
comeGame(){
var bgSound = this.$tools.getLocal('bgSound');
var otherSound = this.$tools.getLocal('otherSound');
if(bgSound){
bgSound=="true"?this.musicArr[0].open=true:this.musicArr[0].open=false;
this.$store.commit('BGSOUND',bgSound=="true"?true:false);
}else{
this.$tools.setLocal('bgSound',true);
this.musicArr[0].open=true;
}
if(otherSound){
otherSound=="true"?this.musicArr[1].open=true:this.musicArr[1].open=false;
this.$store.commit('OTHERSOUND',otherSound=="true"?true:false);
}else{
this.$tools.setLocal('otherSound',true);
this.musicArr[1].open=true;
}
if(this.isLoading){
document.removeEventListener('touchstart',this.comeGame)
document.removeEventListener('click',this.comeGame)
this.isLoading = false;
document.querySelector('#audio2').pause();
document.querySelector('#audio3').pause();
document.querySelector('#audio4').pause();
document.querySelector('#audio2').muted = false;
document.querySelector('#audio3').muted = false;
document.querySelector('#audio4').muted = false;
var musicEle0 = document.getElementById('audio1');
if(bgSound=="true"){
document.querySelector('#audio1').muted = false;
musicEle0.load()
musicEle0.play();
}else{
musicEle0.pause();
}
this.$store.commit('ISLOAD',true);
}
},
touchStart(){
console.log(9)
var musicEle0 = document.getElementById('audio1');
musicEle0.load()
musicEle0.play();

document.removeEventListener('touchstart',this.touchStart)
},
preload(){
let imgs = [

'static/img/desk.png',
"static/img/outback.png",
"static/img/back.png",
"static/img/backe.png",
"static/img/bag.png",
"static/img/bcb.png",
"static/img/cn.png",
"static/img/dc.png",
"static/img/en.png",
"static/img/girl.png",
"static/img/hand.png",
"static/img/money.png",
"static/img/moneybg.jpg",
"static/img/round.png",
"static/img/seletc.png",
"static/img/special.png",
"static/img/usdx.png",
"static/img/xt.png",
"static/img/zc.png",
"static/img/audio.png",
'static/img/alertbg.png',
'static/img/window.png',
'static/img/delete.png',
'static/img/audio-title-en.png',

'static/img/bcb-1.png',
'static/img/bcb-2.png',
'static/img/bcb-3.png',
'static/img/bcb-4.png',
'static/img/bcb-5.png',
'static/img/bcb-6.png',
'static/img/bcb-7.png',
'static/img/dc-1.png',
'static/img/dc-2.png',
'static/img/dc-3.png',
'static/img/dc-4.png',
'static/img/dc-5.png',
'static/img/dc-6.png',
'static/img/dc-7.png',
'static/img/usdx-1.png',
'static/img/usdx-2.png',
'static/img/usdx-3.png',
'static/img/usdx-4.png',
'static/img/usdx-5.png',
'static/img/usdx-6.png',
'static/img/usdx-7.png',
'static/img/xt-1.png',
'static/img/xt-2.png',
'static/img/xt-3.png',
'static/img/xt-4.png',
'static/img/xt-5.png',
'static/img/xt-6.png',
'static/img/xt-7.png',
'static/img/tb_touzi_1.png',
'static/img/tb_touzi_2.png',
'static/img/tb_touzi_3.png',
'static/img/tb_touzi_4.png',
'static/img/tb_touzi_5.png',
'static/img/tb_touzi_6.png',
'static/img/tb-dance-1.png',
'static/img/tb-dance-2.png',
'static/img/tb-dance-3.png',
'static/img/tb-dance-4.png',
'static/img/tb-dance-5.png',
'static/img/tb-dance-6.png',
'static/img/tb-jump-1.png',
'static/img/tb-jump-2.png',
'static/img/tb-jump-3.png',
'static/img/tb-jump-4.png',
'static/img/tb-jump-5.png',
'static/img/tb-jump-6.png',
'static/img/tb-jump-7.png',
'static/img/bcb-bet.png',
'static/img/dc-bet.png',
'static/img/mine-bet.png',
'static/img/usdx-bet.png',
'static/img/xt-bet.png',
























































]
for (let img of imgs) {
let image = new Image()
image.src = img
image.onload = () => {
this.count++;
this.loadNum = (this.count/imgs.length *100).toFixed(0);
if(this.loadNum==100){
this.loadNum = 98;
setTimeout(()=>{

this.loadNum = 100;
this.isLoading = true;
},3000)
}
}
}
},
getWallet(){
this.langChange?this.lanage = 'Zh' : this.lanage = 'Cn';
let walletObj = null;
let timer = setInterval(()=>{
if(walletObj==null){
walletObj = JSON.parse(window.sessionStorage.getItem('playerAddr'));
}else{
clearInterval(timer);
let arr = [];
for(let v in walletObj){
arr.push(JSON.parse(walletObj[v]));
}
let thisAddr = window.localStorage.getItem('thisAddr');
if(thisAddr=='undefined'||thisAddr=='null'||!thisAddr){
this.$store.commit('WALLETADRESS',arr[0]);
this.$store.commit('SHOWWALLET',true);
sessionStorage.setItem('walletIndex',0);
}else{
let hasWallet = false;
let thisAddrObj = JSON.parse(thisAddr);
for(let v in arr){
if(thisAddrObj.address==arr[v].address){
this.$store.commit('WALLETADRESS',arr[v]);
this.$store.commit('SHOWWALLET',false);
sessionStorage.setItem('walletIndex',v);
hasWallet = true;
continue;
}
}
if(!hasWallet){
this.$store.commit('WALLETADRESS',arr[0]);
this.$store.commit('SHOWWALLET',true);
sessionStorage.setItem('walletIndex',0);
}
}
}
},300)
},
outGame(){
window.exitGame();
},
changeLang(){
this.langChange?this.$i18n.locale = 'en' : this.$i18n.locale = 'zh';
window.localStorage.setItem('lang',this.$i18n.locale=='en'?'en-US':'zh-CN')
this.$store.commit('LANGCHANGE',!this.langChange);
},
goLottery(parmas){

this.loading = true;

this.$router.push('/sicbo');
this.$store.commit('PAYUNIT',parmas.item.val)




},

contractAdress(){
this.conAddrData = [],
this.$http.get(this.$req + '/contract-balance?currency=bcb').then(res=>{
res.data.symbol = 'BCB';
this.conAddrData.push(res.data);
this.$http.get(this.$req + '/contract-balance?currency=dc').then(res=>{
res.data.symbol = 'DC';
this.conAddrData.push(res.data);
this.$http.get(this.$req + '/contract-balance?currency=xt').then(res=>{
res.data.symbol = 'XT';
this.conAddrData.push(res.data);
this.$http.get(this.$req + '/contract-balance?currency=usdx').then(res=>{
res.data.symbol = 'USDX';
this.conAddrData.push(res.data);
})
})
})
})
},

openWallet(){
this.$store.commit('SHOWWALLET',true);
},

contractMoney(){
this.capitalPop = true;
},

closeCapital(){
this.capitalPop = false;
},
setVoic(){
console.log(0)
this.audioKey = true;
},
closeAudio(){
this.audioKey = false;
},

voicCtr(parmas){
console.log(parmas)
var musicEle0 = document.getElementById('audio1');
parmas.item.open = !parmas.item.open;
if(parmas.index==0){
this.$tools.setLocal('bgSound',parmas.item.open);
this.$store.commit('BGSOUND',parmas.item.open)
if(parmas.item.open){
musicEle0.load()
musicEle0.play();
}else{
musicEle0.pause();
}
}else{
this.$tools.setLocal('otherSound',parmas.item.open);
this.$store.commit('OTHERSOUND',parmas.item.open)
document.querySelector('#audio2').load();
document.querySelector('#audio3').load();
document.querySelector('#audio4').load();
document.querySelector('#audio2').pause();
document.querySelector('#audio3').pause();
document.querySelector('#audio4').pause();
}
},
}
}




